<script setup>
    import { defineEmits, ref } from 'vue'

    // テスト用のjsonをインポートし，問題番号を初期化
    import question_list from './test_data_questions.json';
    let num_q = 0;
    console.log(question_list);

    // textareaの代入先
    const question_text = ref(question_list[num_q]["normalized"]);

    // 子コンポーネントでの動作を親コンポーネントに伝達させて，親コンポーネントでイベント発火させるためのemit
    const emit = defineEmits(['notify']);
    // 親コンポーネントに動作を伝達させる関数，'input-submitted'は親コンポーネントでのイベントトリガー名
    function handleSubmit() {
        emit('input-submitted', question_text.value);
    }

    // テスト用．次の問題を表示．
    function moveNextQuestion() {
        console.log("in moveNextQuestion()")
        if(num_q == question_list.length-1) {
            return
        }
        num_q++
        console.log("num_q", num_q)
        console.log("question_list[num_q][\"normalized\"]", question_list[num_q]["normalized"])
        question_text.value = question_list[num_q]["normalized"]
    }
    // テスト用．前の問題を表示．
    function movePreviousQuestion() {
        console.log("in movePreviousQuestion()")
        if(num_q == 0) {
            return
        }
        num_q--
        console.log("num_q", num_q)
        console.log("question_list[num_q][\"normalized\"]", question_list[num_q]["normalized"])
        question_text.value = question_list[num_q]["normalized"]
    }
</script>



<template>
    <button @click="moveNextQuestion()">nextQ →</button>
    <button @click="movePreviousQuestion()">← previousQ</button>
    <p>test question No.{{ num_q + 1 }}</p>
    <textarea class="textarea-01" placeholder="空間図形の問題を入力してください" v-model="question_text" @keydown.ctrl.enter="handleSubmit()"></textarea>
    <button class="button-send_to_GPT" @click="handleSubmit()">Create GGB Script</button>
</template>



